<template>
  <div>
    <h2>{{ ciudad }}</h2>
    <h2>Fecha: {{ fecha }}</h2>
    <p>Región: {{ region }}</p>
    <p>País: {{ país }}</p>
    <p>Latitud: {{ latitud }}</p>
    <p>Longitud: {{ longitud }}</p>
    <p>Temperatura promedio: {{ temperaturaPromedio }}°</p>
    <p>Temperatura máxima: {{ temperaturaMáxima }}°C</p>
    <p>Temperatura mínima: {{ temperaturaMínima }}°C</p>
    <p>Condición: {{ condición }}</p>
  </div>
</template>

<script>
export default {
  props: {
    fecha: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      ciudad: '',
      region: '',
      país: '',
      condición: '',
      latitud: 0,
      longitud: 0,
      temperaturaPromedio: 0,
      temperaturaMáxima: 0,
      temperaturaMínima: 0,
    };
  },
  mounted() {
    this.fetchWeatherData();
  },
  watch: {
    fecha: {
      immediate: true,
      handler() {
        this.fetchWeatherData();
      },
    },
  },
  methods: {
    fetchWeatherData() {
      const weatherApiUrl = 'https://api.weatherapi.com/v1/future.json';
      const ubicación = 'Trujillo';
      const apiKey = '8f2de44a3bab4bfba52163352232906';
      const fecha = this.fecha;

      fetch(`${weatherApiUrl}?key=${apiKey}&q=${ubicación}&lang=es&aqi=yes&dt=${fecha}`)
        .then(response => response.json())
        .then(data => {
          const { location, forecast } = data;
          this.ciudad = location.name;
          this.region = location.region;
          this.país = location.country;
          this.condición = forecast.forecastday[0].day.condition.text;
          this.latitud = location.lat;
          this.longitud = location.lon;
          this.temperaturaPromedio = forecast.forecastday[0].day.avgtemp_c;
          this.temperaturaMáxima = forecast.forecastday[0].day.maxtemp_c;
          this.temperaturaMínima = forecast.forecastday[0].day.mintemp_c;
        })
        .catch(error => {
          console.error(error);
        });
    },
  },
};
</script>
